import React from 'react';

function Task(props) {
    const task = props.task
    return (
        <div className='Task'>
            <div>
                <h3 className='TaskName'>{task.name}</h3>
                <p className='TaskAddress'>{task.address}</p>
            </div>
            <div className='RemoveDiv'>
                <button className='RemoveButton' onClick={() => (
                    props.onRemoveTask(task)
                )}>Remove</button>
            </div>
        </div>
    )
}

export default Task;