import React, { Component } from 'react';
import Title from './Title';
import TaskList from './TaskList'
import AddTask from './AddTask';
import { Route } from 'react-router-dom';



class Main extends Component {
    constructor() {
        super()

        this.state = {
            tasks: []
        }
        this.removeTask = this.removeTask.bind(this);

    }
    removeTask(TaskRemoved) {
        console.log(TaskRemoved.name);
        this.setState((state) => (

            {
                tasks: state.tasks.filter(task => task !== TaskRemoved)
            }
        )
        )
    }
    addTask(TaskAdded) {
        this.setState((state) => (

            {
                tasks: state.tasks.concat([TaskAdded])
            }
        )
        )
    }

    render() {
        return (
            <div>

                <Title />
                <Route exact path='/' render={() => (

                    <TaskList tasks={this.state.tasks} onRemoveTask={this.removeTask} />
                )} />
                <Route path='/Addtask' render={({ history }) => (

                    <AddTask onAddTask={(addedTask) => {
                        console.log(addedTask);
                        this.addTask(addedTask);
                        history.push('/')
                    }} />

                )} />
            </div>
        )
    }
}

export default Main;